var helpers = require('@/helpers/functions')

module.exports = function(store) {
  return {
    computed: {
      pageItems() {
        return store.getters.pageItems(this.itemsPerPage)
      },
      totalRows() {
        return store.getters.totalRows
      },
      pages() {
        return store.getters.totalPages(this.itemsPerPage)
      },
      page: {
        get: () => {
          return store.getters.page
        },
        set: (pageNumber) => {
          store.commit('setPage', pageNumber)
        },
      },
    },
    methods: {
      setItemsPerPage: helpers.debounce(function(event) {
        var count = +event.target.value
        store.commit('setPage', 1)
        if (!isNaN(count) && count > 0) {
          this.itemsPerPage = count
        } else {
          this.itemsPerPage = 10
        }
      }, 300),
    },
  }
}



